
import { defineComponent, ref, watch } from "vue"

export default defineComponent({
  props: ["myProductList", "curPage", "pageSize"],
  emits: ["curPageChange"],
  setup(props, { emit }) {
    let newCurPage = ref(props.curPage)
    watch(newCurPage, () => emit("curPageChange", newCurPage.value))
    return {
      newCurPage
    }
  }
})
